<template>
	<div class="return-line clearfix">
		<a @touchend="returnF(-1)" class="return-btn bg fl" href="javascript:void(0)" target="_self"></a>
		<span class="fl">与"{{search}}"相关的视频</span>
	</div>
	<ul v-if="result.video&&result.video.count>0" class="video-list">
		<li v-for="ovideo in result.video.data">
			<a :class="[$index==result.video.data.length-1?'last':'']" class="clearfix" href="{{ovideo.url}}" target="_blank">
				<div class="video-img">
					<img :src="ovideo.img"/>
					<i class="bg"></i>
				</div>
				<div class="video-info">
					<h5>{{{ovideo.title}}}</h5>
					<p><span v-text="ovideo.date | dateSlice ' '" class="time-icon"></span><span class="views-icon">{{ovideo.views}}次浏览</span></p>
				</div>
			</a>
		</li>
	</ul>
</template>
<script type="text/javascript">
import modelObj from "../js/modelObj.js";
	export default{
		data(){
			return {
				page:1,
				result:{},
				search:modelObj.search
			}
		},
		created(){
			let that = this;
			console.log(that)
			let url = "http://3g.d.cn/api/game/search.json?id="+modelObj.rId+"&keyword="+modelObj.search+"&type=video"+"&isHot="+modelObj.hot;
			that.$http.jsonp(url).then((response)=>{
				// success
				//alert("ok")
				console.log(response.data.video.data.length,response.data.video.count)
				if((response.data.video.data.length)>=20&&(response.data.video.count)>20){
					console.log("pullLoad")
					that.max = Math.ceil(response.data.video.count/20);
					that.pullLoad();
				}
				that.result = response.data;
			},(response)=>{
				//error
				console.log(response)
			})
		},
		methods:{
			returnF(val){
				window.history.go(val)
			},
			pullLoad(){
				var that = this;
				that.timer = null;
				window.addEventListener("scroll",that.loadF,false)
			},
			loadF(){
				let that = this;
					if(that.timer){
						clearTimeout(that.timer);
					}
					that.timer = setTimeout(function(){
						//console.log(window.screen.availHeight+document.body.scrollTop,document.body.clientHeight-100)
						if(window.screen.availHeight+document.body.scrollTop >= document.body.clientHeight-100){
							that.page += 1;
							if(that.page<=that.max){
								let url = "http://3g.d.cn/api/game/search.json?id="+modelObj.rId+"&keyword="+modelObj.search+"&type=video"+"&isHot="+modelObj.hot+"&page="+that.page;
								that.$http.jsonp(url).then((response)=>{
									debugger
									that.result.video.data=that.result.video.data.concat(response.data.video.data);
								},(response)=>{
									//error
									console.log(response)
								})
							}else{
								//移除监听
								window.removeEventListener("scroll",that.loadF,false);
							}
						}
						
					},300)
				
			}
		},
		filters:{
			tofixed:modelObj.tofixed,
			hightLight:modelObj.hightLight,
			dateSlice:modelObj.dateSlice
		}
	}
</script>


